import axios, { AxiosInstance, AxiosError, InternalAxiosRequestConfig } from 'axios';
import { BlogPost, ContactFormData } from '../types';
import { ErrorResponse } from '../types/api.types';
import logger from '../utils/logger';

// API URL  (   8000)
const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:8000/api';
const API_TIMEOUT = Number(process.env.REACT_APP_API_TIMEOUT) || 30000;

const axiosInstance: AxiosInstance = axios.create({
  baseURL: API_URL,
  timeout: API_TIMEOUT,
  headers: {
    'Content-Type': 'application/json',
  },
  withCredentials: true, // CORS  
});

// Error message mapping
const getErrorMessage = (status: number): string => {
  const errorMessages: Record<number, string> = {
    400: ' .',
    401: ' .',
    403: ' .',
    404: '    .',
    429: '   .    .',
    500: '  .    .',
    502: '  .',
    503: '    .',
  };
  return errorMessages[status] || '    .';
};

// Custom error interface
interface CustomAxiosError extends AxiosError {
  userMessage?: string;
  _retry?: boolean;
}

// Request interceptor
axiosInstance.interceptors.request.use(
  (config: InternalAxiosRequestConfig) => {
    // Security: Always use HTTPS in production
    if (process.env.NODE_ENV === 'production' && config.url && config.url.startsWith('http://')) {
      logger.warn('Insecure HTTP detected in production, upgrading to HTTPS');
      config.url = config.url.replace('http://', 'https://');
    }
    if (
      process.env.NODE_ENV === 'production' &&
      config.baseURL &&
      config.baseURL.startsWith('http://')
    ) {
      logger.warn('Insecure HTTP in baseURL detected in production, upgrading to HTTPS');
      config.baseURL = config.baseURL.replace('http://', 'https://');
    }

    // Add auth token if available
    const token = localStorage.getItem('authToken');
    if (token && config.headers) {
      config.headers.Authorization = `Bearer ${token}`;
    }

    // Development logging can be enabled if needed
    // logger.debug('API Request:', `${config.method?.toUpperCase()} ${config.url}`);

    return config;
  },
  (error: AxiosError) => {
    return Promise.reject(error);
  }
);

// Response interceptor
axiosInstance.interceptors.response.use(
  response => {
    // Development logging can be enabled if needed
    return response;
  },
  async (error: CustomAxiosError) => {
    const originalRequest = error.config;

    // Handle timeout errors
    if (error.code === 'ECONNABORTED' && originalRequest && !error._retry) {
      error._retry = true;
      logger.warn('Request timeout, retrying...');
      return axiosInstance(originalRequest);
    }

    // Handle network errors
    if (!error.response) {
      error.message = '  .';
      return Promise.reject(error);
    }

    // Handle HTTP errors
    const { status, data } = error.response;
    const errorData = data as ErrorResponse;
    error.userMessage = errorData?.message || getErrorMessage(status);

    // Log error in development
    if (process.env.NODE_ENV === 'development') {
      logger.error('API Error:', `${status} ${error.userMessage}`);
    }

    // Handle 401 - Clear auth and redirect to login if needed
    if (status === 401) {
      localStorage.removeItem('authToken');
      // window.location.href = '/login'; // Uncomment when login page exists
    }

    return Promise.reject(error);
  }
);

// API response interfaces
interface PaginatedResponse<T> {
  count: number;
  next: string | null;
  previous: string | null;
  results: T[];
}

interface Project {
  id: number;
  title: string;
  description: string;
  technologies: string[];
  link?: string;
}

// API methods with improved error handling
export const api = {
  // Projects
  getProjects: () => axiosInstance.get<Project[]>('projects/'),
  createProject: (data: Partial<Project>) => axiosInstance.post<Project>('projects/', data),

  // Blog
  getBlogPosts: (page: number = 1, pageSize?: number) => {
    const size = pageSize || Number(process.env.REACT_APP_POSTS_PER_PAGE) || 6;
    return axiosInstance.get<PaginatedResponse<BlogPost>>(
      `blog-posts/?page=${page}&page_size=${size}`
    );
  },
  getBlogPost: (id: number | string) => axiosInstance.get<BlogPost>(`blog-posts/${id}/`),
  searchBlogPosts: (query: string) =>
    axiosInstance.get<PaginatedResponse<BlogPost>>(`blog-posts/?search=${query}`),
  getBlogCategories: () => axiosInstance.get<string[]>('categories/'),

  // Contact
  createContact: async (data: ContactFormData) => {
    try {
      const response = await axiosInstance.post('contact/', data);
      return response;
    } catch (error) {
      // Add specific handling for contact errors
      const axiosError = error as CustomAxiosError;
      if (axiosError.response?.status === 429) {
        axiosError.userMessage = '   . 1   .';
      }
      throw axiosError;
    }
  },

  // Newsletter
  subscribeNewsletter: (email: string) => axiosInstance.post('newsletter/', { email }),

  // Health check
  checkHealth: () => axiosInstance.get<{ status: string }>('health/'),
};

// Export specific services for backward compatibility
export const blogService = {
  getPosts: api.getBlogPosts,
  getPost: api.getBlogPost,
  searchPosts: api.searchBlogPosts,
  getCategories: api.getBlogCategories,
  createPost: (data: Partial<BlogPost>) => axiosInstance.post<BlogPost>('blog-posts/', data),
  updatePost: (id: number | string, data: Partial<BlogPost>) =>
    axiosInstance.put<BlogPost>(`blog-posts/${id}/`, data),
  deletePost: (id: number | string) => axiosInstance.delete(`blog-posts/${id}/`),
};

// Export axios instance for custom requests
export default axiosInstance;
