import React, { memo } from 'react';
import { motion } from 'framer-motion';
import {
  SkeletonHero,
  SkeletonServices,
  SkeletonBlogList,
  SkeletonForm,
  SkeletonCard,
} from './SkeletonLoader';

//   
export type LoadingVariant =
  | 'spinner' //  
  | 'page' //   ( )
  | 'inline' //   ( )
  | 'skeleton-hero' // Hero 
  | 'skeleton-services' // Services 
  | 'skeleton-blog' // Blog 
  | 'skeleton-form' // Form 
  | 'skeleton-card' // Card 
  | 'dots' //  
  | 'pulse'; //  

interface UnifiedLoadingProps {
  variant?: LoadingVariant;
  message?: string;
  fullScreen?: boolean;
  size?: 'sm' | 'md' | 'lg' | 'xl';
  className?: string;
  color?: string;
}

const UnifiedLoading: React.FC<UnifiedLoadingProps> = memo(
  ({
    variant = 'spinner',
    message,
    fullScreen = false,
    size = 'md',
    className = '',
    color = 'indigo',
  }) => {
    //  
    const sizeClasses = {
      sm: 'w-8 h-8',
      md: 'w-12 h-12',
      lg: 'w-16 h-16',
      xl: 'w-20 h-20',
    };

    //  
    const colorClasses = {
      indigo: 'border-indigo-600',
      blue: 'border-blue-600',
      gray: 'border-gray-600',
      green: 'border-green-600',
    };

    //   
    const FullScreenWrapper: React.FC<{ children: React.ReactNode }> = ({ children }) => {
      if (fullScreen || variant === 'page') {
        return (
          <div className="fixed inset-0 bg-white bg-opacity-90 z-50 flex items-center justify-center">
            {children}
          </div>
        );
      }
      return <>{children}</>;
    };

    //   
    if (variant.startsWith('skeleton-')) {
      switch (variant) {
        case 'skeleton-hero':
          return <SkeletonHero className={className} />;
        case 'skeleton-services':
          return <SkeletonServices className={className} />;
        case 'skeleton-blog':
          return <SkeletonBlogList className={className} />;
        case 'skeleton-form':
          return <SkeletonForm className={className} />;
        case 'skeleton-card':
          return <SkeletonCard className={className} />;
      }
    }

    //  
    if (variant === 'dots') {
      return (
        <FullScreenWrapper>
          <div className={`flex items-center justify-center space-x-2 ${className}`}>
            {[0, 1, 2].map(index => (
              <motion.div
                key={index}
                className={`w-3 h-3 bg-${color}-600 rounded-full`}
                animate={{
                  y: [0, -10, 0],
                  opacity: [1, 0.5, 1],
                }}
                transition={{
                  duration: 0.6,
                  repeat: Infinity,
                  delay: index * 0.2,
                  ease: 'easeInOut',
                }}
              />
            ))}
            {message && <span className="ml-4 text-gray-600">{message}</span>}
          </div>
        </FullScreenWrapper>
      );
    }

    //  
    if (variant === 'pulse') {
      return (
        <FullScreenWrapper>
          <div className={`flex flex-col items-center justify-center ${className}`}>
            <motion.div
              className={`${sizeClasses[size]} bg-${color}-600 rounded-full`}
              animate={{
                scale: [1, 1.5, 1],
                opacity: [1, 0.5, 1],
              }}
              transition={{
                duration: 1.5,
                repeat: Infinity,
                ease: 'easeInOut',
              }}
            />
            {message && <p className="mt-4 text-gray-600">{message}</p>}
          </div>
        </FullScreenWrapper>
      );
    }

    //  
    if (variant === 'inline') {
      return (
        <span className={`inline-flex items-center ${className}`}>
          <motion.span
            className={`inline-block w-4 h-4 border-2 ${colorClasses[color as keyof typeof colorClasses] || colorClasses.indigo} border-t-transparent rounded-full`}
            animate={{ rotate: 360 }}
            transition={{
              duration: 1,
              repeat: Infinity,
              ease: 'linear',
            }}
          />
          {message && <span className="ml-2 text-sm text-gray-600">{message}</span>}
        </span>
      );
    }

    //   (spinner, page)
    return (
      <FullScreenWrapper>
        <div className={`flex flex-col items-center justify-center ${className}`}>
          <motion.div
            animate={{
              rotate: 360,
            }}
            transition={{
              duration: 1,
              repeat: Infinity,
              ease: 'linear',
            }}
            className={`${sizeClasses[size]} border-4 ${colorClasses[color as keyof typeof colorClasses] || colorClasses.indigo} border-t-transparent rounded-full`}
          />
          {message && (
            <motion.p
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.2 }}
              className="mt-4 text-gray-600"
            >
              {message}
            </motion.p>
          )}
        </div>
      </FullScreenWrapper>
    );
  }
);

UnifiedLoading.displayName = 'UnifiedLoading';

//    
export const PageLoading: React.FC<{ message?: string }> = ({
  message = '  ...',
}) => <UnifiedLoading variant="page" message={message} />;

export const InlineLoading: React.FC<{ message?: string }> = ({ message }) => (
  <UnifiedLoading variant="inline" message={message} size="sm" />
);

export const ButtonLoading: React.FC = () => (
  <UnifiedLoading variant="inline" size="sm" color="white" />
);

export default UnifiedLoading;
