/**
 *   
 *  console.log 
 */

type LogLevel = 'debug' | 'info' | 'warn' | 'error';

interface LoggerConfig {
  enableInProduction: boolean;
  logLevel: LogLevel;
}

class Logger {
  private config: LoggerConfig;
  private isDevelopment: boolean;

  constructor(config?: Partial<LoggerConfig>) {
    this.isDevelopment = process.env.NODE_ENV === 'development';
    this.config = {
      enableInProduction: false,
      logLevel: 'debug',
      ...config,
    };
  }

  private shouldLog(level: LogLevel): boolean {
    if (!this.isDevelopment && !this.config.enableInProduction) {
      return false;
    }

    const levels: LogLevel[] = ['debug', 'info', 'warn', 'error'];
    const currentLevelIndex = levels.indexOf(this.config.logLevel);
    const messageLevelIndex = levels.indexOf(level);

    return messageLevelIndex >= currentLevelIndex;
  }

  private formatMessage(level: LogLevel, message: string, _data?: unknown): string {
    const timestamp = new Date().toISOString();
    return `[${timestamp}] [${level.toUpperCase()}] ${message}`;
  }

  debug(message: string, data?: unknown): void {
    if (this.shouldLog('debug')) {
      // eslint-disable-next-line no-console
      console.log(this.formatMessage('debug', message), data || '');
    }
  }

  info(message: string, data?: unknown): void {
    if (this.shouldLog('info')) {
      // eslint-disable-next-line no-console
      console.info(this.formatMessage('info', message), data || '');
    }
  }

  warn(message: string, data?: unknown): void {
    if (this.shouldLog('warn')) {
      console.warn(this.formatMessage('warn', message), data || '');
    }
  }

  error(message: string, error?: Error | unknown): void {
    if (this.shouldLog('error')) {
      console.error(this.formatMessage('error', message), error || '');

      //     
      if (!this.isDevelopment && error) {
        this.reportToErrorService(message, error);
      }
    }
  }

  //  
  group(label: string): void {
    if (this.isDevelopment) {
      // eslint-disable-next-line no-console
      console.group(label);
    }
  }

  groupEnd(): void {
    if (this.isDevelopment) {
      // eslint-disable-next-line no-console
      console.groupEnd();
    }
  }

  //  
  table(data: unknown): void {
    if (this.isDevelopment) {
      // eslint-disable-next-line no-console
      console.table(data);
    }
  }

  //  
  time(label: string): void {
    if (this.isDevelopment) {
      // eslint-disable-next-line no-console
      console.time(label);
    }
  }

  timeEnd(label: string): void {
    if (this.isDevelopment) {
      // eslint-disable-next-line no-console
      console.timeEnd(label);
    }
  }

  //     (Sentry, LogRocket )
  private reportToErrorService(_message: string, _error: unknown): void {
    //    Sentry   
    // if (window.Sentry) {
    //   window.Sentry.captureException(error, {
    //     tags: { message }
    //   });
    // }
  }

  //   
  logRequest(method: string, url: string, data?: unknown): void {
    if (this.isDevelopment) {
      this.group(` ${method} ${url}`);
      if (data) {
        this.debug('Request Data:', data);
      }
      this.groupEnd();
    }
  }

  //   
  logResponse(method: string, url: string, status: number, data?: unknown): void {
    if (this.isDevelopment) {
      const emoji = status >= 200 && status < 300 ? '' : '';
      this.group(`${emoji} ${method} ${url} - ${status}`);
      if (data) {
        this.debug('Response Data:', data);
      }
      this.groupEnd();
    }
  }
}

//  
const logger = new Logger({
  enableInProduction: process.env.REACT_APP_ENABLE_LOGGING === 'true',
  logLevel: (process.env.REACT_APP_LOG_LEVEL as LogLevel) || 'info',
});

export default logger;

//   export
export const { debug, info, warn, error, group, groupEnd, table, time, timeEnd } = logger;
