import React, { memo } from 'react';
import { Helmet } from 'react-helmet-async';

interface SEOHelmetProps {
  title?: string;
  description?: string;
  keywords?: string;
  author?: string;
  image?: string;
  url?: string;
  type?: string;
}

const SEOHelmet: React.FC<SEOHelmetProps> = memo(
  ({
    title = '',
    description = 'AI      ',
    keywords = 'AI , , , LLM,  , AI , , emelmujiro, ChatGPT,  ,  , AI , ',
    author = '',
    image = 'https://researcherhojin.github.io/emelmujiro/og-image.png',
    url = 'https://researcherhojin.github.io/emelmujiro',
    type = 'website',
  }) => {
    const siteTitle = title === '' ? title : `${title} | `;

    return (
      <Helmet>
        {/* Basic Meta Tags */}
        <title>{siteTitle}</title>
        <meta name="description" content={description} />
        <meta name="keywords" content={keywords} />
        <meta name="author" content={author} />
        <meta name="viewport" content="width=device-width, initial-scale=1.0" />
        <meta charSet="utf-8" />

        {/* Open Graph Meta Tags */}
        <meta property="og:title" content={siteTitle} />
        <meta property="og:description" content={description} />
        <meta property="og:image" content={image} />
        <meta property="og:url" content={url} />
        <meta property="og:type" content={type} />
        <meta property="og:site_name" content="" />
        <meta property="og:locale" content="ko_KR" />

        {/* Twitter Card Meta Tags */}
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content={siteTitle} />
        <meta name="twitter:description" content={description} />
        <meta name="twitter:image" content={image} />

        {/* Canonical URL */}
        <link rel="canonical" href={url} />

        {/* Language */}
        <html lang="ko" />

        {/* Additional SEO Tags */}
        <meta
          name="robots"
          content="index, follow, max-image-preview:large, max-snippet:-1, max-video-preview:-1"
        />
        <meta
          name="googlebot"
          content="index, follow, max-image-preview:large, max-snippet:-1, max-video-preview:-1"
        />
        <meta name="bingbot" content="index, follow" />
        <meta name="revisit-after" content="7 days" />
        <meta name="rating" content="general" />
        <meta name="distribution" content="global" />

        {/* Additional Open Graph Tags */}
        <meta property="og:image:width" content="1200" />
        <meta property="og:image:height" content="630" />
        <meta property="og:image:alt" content=" - AI     " />

        {/* Twitter additional tags */}
        <meta name="twitter:image:alt" content=" - AI     " />

        {/*   */}
        <meta httpEquiv="content-language" content="ko" />

        {/* Schema.org JSON-LD */}
        <script type="application/ld+json">
          {JSON.stringify({
            '@context': 'https://schema.org',
            '@type': 'Organization',
            name: '',
            description: description,
            url: 'https://researcherhojin.github.io/emelmujiro',
            logo: 'https://researcherhojin.github.io/emelmujiro/logo512.png',
            founder: {
              '@type': 'Person',
              name: '',
            },
            contactPoint: {
              '@type': 'ContactPoint',
              email: 'researcherhojin@gmail.com',
              telephone: '+82-10-7279-0380',
              contactType: 'customer service',
              availableLanguage: ['Korean', 'English'],
            },
            address: {
              '@type': 'PostalAddress',
              addressCountry: 'KR',
              addressLocality: '',
            },
            sameAs: ['https://github.com/researcherhojin', 'https://emelmujiro.com'],
            foundingDate: '2022',
            areaServed: '',
            serviceType: ['AI ', ' ', 'LLM  '],
          })}
        </script>
      </Helmet>
    );
  }
);

SEOHelmet.displayName = 'SEOHelmet';

export default SEOHelmet;
